
.sidebar {
    width: 25%;
    height: 100vh;
    background: rgb(48, 48, 56);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: fixed;
    top: 0;
    transition: all .4s;
    &-title {
        font-size: 42px;
        margin: 0 0 20px;
    }
    &-nav {
        li{
            display: flex;
            align-items: center;
            margin: 10px 0;
        }
        &__item {
            text-decoration: none;
            color: white;
            font-size: 22px;
        }
        &__img {
            width: 35px;
            height: 35px;
            margin: 0 10px 0 0;
        }
    }
}

@media (max-width: 991px) {
    .sidebar {
        opacity: 0;
        overflow: hidden;
        transform: translate(-100%, 0);
        &-button {
            width: 35px;
            height: 45px;
            transform: rotate(270deg);
            transition: all .4s;
            cursor: pointer;
            & span {
               &::before {
                content: url('assets/img/arrow.svg');
                display: block;
                width: 35px;
                height: 35px;
               }
            }
        }
    }
    .sidebar.active {
        opacity: 1;
        overflow: visible;
        transform: translate(0, 0);
        width: 75%;
    }
    .sidebar-button.active {
        margin: 0 75%;
        transform: rotate(90deg);
    }
}

